import { Accordion, AccordionDetails, AccordionSummary, Button, FormControl, List, ListItem, ListItemIcon, ListItemSecondaryAction, ListItemText, Paper, Select, TextField, Typography } from "@material-ui/core";
import { navigate } from "gatsby";
import * as React from "react";
import ajax, { useFetcher } from "shared/components/ajax";
import { useModalTheme } from "shared/components/style";
import Defer from "shared/components/ui/Defer";
import PinPrompt from "shared/components/ui/market/PinPrompt";
import Navbar from "shared/components/ui/Navbar";
import { buildSearchParam, rupiah } from "shared/components/util";
// @ts-ignore
import icon from 'shared/images/Icon PDAM.png';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import TwoRow from "../../../../shared/components/ui/TwoRow";

const Page = () => {
    const modalTheme = useModalTheme();
    const [cust, setCust] = React.useState(null);
    const [prov, setProv] = React.useState(null);
    const [kab, setKab] = React.useState(null);
    const [prompt, setPrompt] = React.useState(0);
    const geolookup = useFetcher('/biller/pdam/geolookup');
    const provlookup = React.useMemo(() => {
        if (prov && geolookup)
            return geolookup.data.find(x => x.id === prov);
        return null;
    }, [geolookup, prov]);

    const data = useFetcher(cust ? '/biller/pdam/postpaid?' + buildSearchParam({
        custid: cust,
        geolookup: kab,
    }) : null, null, (err) => {
        alert(err.message);
        setCust(null);
    });

    const check = (e) => {
        e.preventDefault();
        setCust(e.currentTarget.account.value);
    }
    const submit = (pin) => {
        setPrompt(3);
        ajax.post('/biller/pdam/postpaid', {
            custid: data.custid,
            geolookup: kab,
            pin: pin,
        }).then(x => {
            alert('Berhasil');
            navigate('/digital/history/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
            setPrompt(2);
        });
    }
    return (
        <div>
            <Navbar />
            <form style={{ padding: '1em' }} onSubmit={check}>
                <List>
                    <ListItem>
                        <ListItemIcon><img src={icon} height="40px" alt="" /></ListItemIcon>
                        <ListItemText primary="Bayar Tagihan PDAM" />
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Provinsi" />
                        <ListItemSecondaryAction>
                            <FormControl disabled={!geolookup} style={{ minWidth: '50vw' }}>
                                <Select native value={prov} onChange={(x) => { setProv(x.target.value); setKab(null); }}>
                                    <option value=""></option>
                                    {geolookup ? geolookup.data.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                </Select>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <ListItemText primary="Kabupaten" />
                        <ListItemSecondaryAction>
                            <FormControl disabled={!provlookup} style={{ minWidth: '50vw' }}>
                                <Select native value={kab} onChange={(x) => { setKab(x.target.value); }}>
                                    <option value=""></option>
                                    {provlookup ? provlookup.descendants.map(x => <option key={x.id} value={x.id}>{x.name}</option>) : []}
                                </Select>
                            </FormControl>
                        </ListItemSecondaryAction>
                    </ListItem>
                    <ListItem>
                        <TextField name="account" disabled={!kab} label="Nomor Meter" variant="filled" fullWidth />
                    </ListItem>
                    <ListItem>
                        <Button type="submit" variant="contained" color="primary">
                            Cek
                        </Button>
                        <Button style={{ marginLeft: 'auto' }} variant="outlined" onClick={() => navigate('/user/')}>
                            Kembali
                        </Button>
                    </ListItem>
                </List>
            </form>
            <Defer data={data} visible={cust}>
                {() => (
                    <Paper elevation={5}><List>
                        <TwoRow label="ID Pelanggan" value={data.custid}  />
                        <TwoRow label="Nama" value={data.details.nama}  />
                        <TwoRow label="Tagihan" value={rupiah(data.total)}  />
                        <TwoRow label="Biaya Admin" value={rupiah(data.admin)}  />
                        <TwoRow label="Total Tagihan" value={rupiah(data.total)} strong />
                        <ListItem>
                            <Accordion style={{ width: '100%' }}>
                                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                    <Typography variant="h6">
                                        Rincian Tagihan
                                    </Typography>
                                </AccordionSummary>
                                <AccordionDetails style={{ flexDirection: 'column' }}>
                                    <List dense>
                                        {data.details.rincian.map(x => (
                                            <>
                                                <TwoRow label="Periode" value={x.periode + ""} />
                                                <TwoRow label="Meter Awal" value={x.meterAwal + ""} />
                                                <TwoRow label="Meter Akhir" value={x.meterAkhir + ""} />
                                                <TwoRow label="Pemakaian" value={x.pemakaian + ""} />
                                                <TwoRow label="Tagihan" value={rupiah(x.tagihan)} />
                                                <TwoRow label="Admin" value={rupiah(x.admin)} />
                                                <TwoRow label="Denda" value={rupiah(x.denda)} />
                                                <TwoRow label="Total" value={rupiah(x.total)} divider />
                                            </>
                                        ))}
                                    </List>
                                </AccordionDetails>
                            </Accordion>
                        </ListItem>
                        <ListItem>
                            <Button fullWidth color="primary" variant="contained"
                                style={{ marginLeft: 'auto' }} onClick={() => setPrompt(2)}>Beli</Button>
                        </ListItem>
                    </List></Paper>
                )}
            </Defer>
            <PinPrompt open={prompt >= 2} submitting={prompt === 3} onClose={() => setPrompt(0)} onAction={submit} />
        </div >
    );
}

export default Page
